.navbar {
  width: 100%;

  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

.logout .btn {
  float: right;
}
.btnLogout {
  width: 100%;
  display: block;
  padding: 8px 15px;
  outline: none;
  text-align: center;
  margin: auto;
  background-color: transparent;
  border: none;
  color: white;
}
.btnLogout:hover:not(.active) {
  background-color: #111;
}
.item {
  display: block;
  padding: 8px 15px;
  color: white;
  text-align: center;
  text-decoration: none;
}

.item:hover:not(.active) {
  background-color: #111;
}
