.container1 {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
}
.Card {
  margin-left: 50px;
}
.div1 {
  justify-self: center;
}
.div3 {
  justify-self: center;
}
.div2 {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  margin-right: auto;
}
.delete {
  background-color: gray; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}
.btn2 {
  margin: 10px;
  outline: none;
  background-color: #484848; /* Green */
  border: none;
  color: white;
  text-align: center;
  text-decoration: none;
  font-size: 20px;
  border-radius: 10px;
}
.btn2:hover {
  background-color: black;
  color: white;
}
.div4 {
  display: grid;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100px;
}
.pagination {
  margin: 15px auto;
  display: flex;
  list-style: none;
  outline: none;
}
.pagination > .active > a {
  background-color: #ba000d;
  border-color: #ba000d;
  color: #fff;
}
.pagination > li > a {
  border: 1px solid #ba000d;
  padding: 5px 10px;
  outline: none;
  cursor: pointer;
}
.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  background-color: #ba000d;
  border-color: #ba000d;
  outline: none;
}
.pagination > li > a,
.pagination > li > span {
  color: #ba000d;
}
.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  border-radius: unset;
}
.search {
  margin: 10px;
  padding: 10px;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.form-container {
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}
.input {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.input,
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}
.submit {
  width: 100%;
  background-color: #ba000d;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
@media (max-width: 800px) {
  .search {
    margin: 10px 0;
  }

  .div2 {
    flex-direction: column;
    align-items: stretch;
  }
}
